import React from 'react';
import '../App.css';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom';

export default function ImgMediaCard() {

    return (
        <div class="card">
            <Link class='links' to='/pixelarts'>
        <Card>
            <CardActionArea>
                <CardMedia
                    component="img"
                    alt="Пиксель-арт"
                    height="140"
                    image="https://mir-s3-cdn-cf.behance.net/projects/404/20aa0458405807.5a3cf03eda3c5.jpg"
                    title="Пиксель-арт"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        Пиксель-арт
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                    Форма цифрового художественного искусства, в котором изменения вносятся на уровне пикселей.
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Card>
        </Link>
        </div>
    );
}
