import React from 'react';
import '../App.css';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom';


export default function ImgMediaCard() {

    return (
        <div class="card">
            <Link class='links' to="/ads">
        <Card>
            <CardActionArea>
                <CardMedia
                    component="img"
                    alt="Реклама"
                    height="140"
                    image="https://www.24print.ua/cache/files/51/31/317a09350625afe5fab75e41c765ae14/800_800/1-min.png"
                    title="Реклама"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        Реклама
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                    Направление в маркетинге в целях привличения внимания с помощью массовой распространение информации.
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Card>
        </Link>
        </div>
    );
}
